import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import Page from 'components/LayoutComponents/Page';
import { TableData, ColEditDelete } from 'components/common';
import { Card, Row, Col } from 'antd';
import { getQueryParams } from 'utils/urlUtil';
import { getListSubjectGroup } from 'services/StaffSubjectGroup';

import routeConstant from 'routes/route/routeConstant';

const ROUTER_FORM_DATA =
  routeConstant.DANH_MUC__CHUONG_TRINH_DAO_TAO__GIAO_VIEN_BO_MON__NHAP_DU_LIEU;

class SubjectGroupPage extends Component {
  state = {
    reload: 0,
    paramSearch: {},
  };

  componentDidMount = () => {
    this.handleChangeQueryParams();
  };

  componentWillReceiveProps = () => {
    this.handleChangeQueryParams();
  };

  urlFormEdit = '#/staff/subjectGroup/form/edit';

  handleChangeQueryParams = () => {
    this.setState({ paramSearch: getQueryParams() });
  };

  render() {
    const columns = [
      {
        title: 'Mã bộ môn',
        dataIndex: 'subjectGroupId',
        key: 'subjectGroupId',
      },
      {
        title: 'Tên bộ môn',
        dataIndex: 'label',
        key: 'label',
      },
      {
        title: 'SL giáo viên',
        dataIndex: 'noOfStaff',
        key: 'noOfStaff',
      },
      {
        title: 'Thao tác',
        dataIndex: 'operation',
        render: (text, record) => (
          <ColEditDelete
            linkEdit={{
              to: {
                pathname: ROUTER_FORM_DATA,
                search: `?id=${record.subjectGroupId}`,
              },
            }}
            hidenBtnDelete="true"
          />
        ),
      },
    ];

    return (
      <Card title="">
        <Row>
          <Col span={24}>
            <TableData
              reload={this.state.reload}
              paramSearch={this.state.paramSearch}
              service={getListSubjectGroup}
              columns={columns}
              rowKey="id"
            />
          </Col>
        </Row>
      </Card>
    );
  }
}

export default withRouter(props => (
  <Page {...props}>
    <SubjectGroupPage {...props} />
  </Page>
));



